.navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 850px;
    width: 100%;
    height: 80px;
    background-color: white;
}
.darkMode{
    width: 100px;
    height: 40px;
    color: white;
    border: 1px solid black;
    border-radius: 7px;
    background-color: black;
    cursor: pointer;
}
.navbardark{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 850px;
    width: 100%;
    height: 80px;
    background-color: black;
    color: white;
    transition: 0.5s;
}
.darkModedark{
    width: 100px;
    height: 40px;
    color: white;
    border: 1px solid black;
    border-radius: 7px;
    background-color: white;
    color: black;
    cursor: pointer;
}